import { useTranslation } from "react-i18next";
import { useState, useMemo } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { useBannedUsers, useUnbanUser } from "@/hooks/useAdmin";
import { UserProfileDialog } from "@/components/admin/UserProfileDialog";
import { Ban, UserCheck, Eye, Users, Briefcase, Shield, Search, X } from "lucide-react";
import { format } from "date-fns";

const AdminBannedUsers = () => {
  const { t } = useTranslation();
  const { data: users, isLoading } = useBannedUsers();
  const unbanUser = useUnbanUser();

  const [searchQuery, setSearchQuery] = useState("");
  const [userProfileDialog, setUserProfileDialog] = useState<{
    open: boolean;
    userId: string;
    userName: string;
    userRole: string;
  }>({ open: false, userId: "", userName: "", userRole: "" });

  const filteredUsers = useMemo(() => {
    if (!users) return [];
    return users.filter((user: any) => {
      const matchesSearch =
        searchQuery === "" ||
        user.display_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        user.ban_reason?.toLowerCase().includes(searchQuery.toLowerCase());
      return matchesSearch;
    });
  }, [users, searchQuery]);

  const getRoleBadgeColor = (role: string) => {
    switch (role) {
      case "admin":
        return "bg-primary text-primary-foreground";
      case "supervisor":
        return "bg-blue-500 text-white";
      case "merchant":
        return "bg-emerald-500 text-white";
      default:
        return "bg-muted text-muted-foreground";
    }
  };

  const getRoleIcon = (role: string) => {
    switch (role) {
      case "admin":
      case "supervisor":
        return <Shield className="h-3.5 w-3.5" />;
      case "merchant":
        return <Briefcase className="h-3.5 w-3.5" />;
      default:
        return <Users className="h-3.5 w-3.5" />;
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center space-y-3">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto"></div>
          <p className="text-muted-foreground">{t("common.loading")}</p>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <div className="flex items-center gap-3 mb-2">
          <div className="p-2 bg-orange-100 dark:bg-orange-900/30 rounded-lg">
            <Ban className="h-6 w-6 text-orange-500" />
          </div>
          <div>
            <h1 className="text-3xl font-bold tracking-tight">{t("admin.bannedManagement")}</h1>
            <p className="text-muted-foreground">{t("admin.bannedManagementDesc")}</p>
          </div>
        </div>
      </div>

      {/* Statistics */}
      <Card className="bg-gradient-to-br from-orange-50 to-orange-100 border-orange-200 dark:from-orange-950/20 dark:to-orange-900/20 dark:border-orange-900/30">
        <CardContent className="pt-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-muted-foreground">{t("admin.totalBanned")}</p>
              <p className="text-2xl font-bold">{users?.length || 0}</p>
            </div>
            <Ban className="h-8 w-8 text-orange-500" />
          </div>
        </CardContent>
      </Card>

      {/* Search */}
      <Card>
        <CardContent className="pt-6">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder={t("admin.searchBannedUserPlaceholder")}
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="pl-10 pr-10"
            />
            {searchQuery && (
              <Button
                variant="ghost"
                size="sm"
                className="absolute right-1 top-1/2 transform -translate-y-1/2 h-7 w-7 p-0"
                onClick={() => setSearchQuery("")}
              >
                <X className="h-4 w-4" />
              </Button>
            )}
          </div>
        </CardContent>
      </Card>

      {/* User List */}
      {filteredUsers && filteredUsers.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {filteredUsers.map((user: any) => (
            <Card key={user.id} className="border-orange-300 bg-orange-50/50 dark:bg-orange-950/20">
              <CardContent className="p-4">
                <div className="flex items-start gap-3">
                  <Avatar className="h-12 w-12">
                    <AvatarImage src={user.avatar_url} alt={user.display_name} />
                    <AvatarFallback>{user.display_name.charAt(0).toUpperCase()}</AvatarFallback>
                  </Avatar>
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2">
                      <h3 className="font-semibold truncate">{user.display_name}</h3>
                      <Badge variant="outline" className="text-xs text-orange-500 border-orange-300">
                        {t("admin.banned")}
                      </Badge>
                    </div>
                    <Badge className={`${getRoleBadgeColor(user.role)} text-xs mt-1`}>
                      {getRoleIcon(user.role)}
                      <span className="ml-1 capitalize">{user.role}</span>
                    </Badge>
                  </div>
                </div>

                {user.ban_reason && (
                  <div className="mt-3 p-2 bg-orange-100 dark:bg-orange-900/30 rounded text-sm">
                    <p className="font-medium text-orange-700 dark:text-orange-300">{t("admin.banReason")}:</p>
                    <p className="text-orange-600 dark:text-orange-400">{user.ban_reason}</p>
                  </div>
                )}

                {user.banned_at && (
                  <p className="text-xs text-muted-foreground mt-2">
                    {t("admin.bannedAt")}: {format(new Date(user.banned_at), "yyyy-MM-dd HH:mm")}
                  </p>
                )}

                <div className="flex gap-2 mt-4 pt-3 border-t">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() =>
                      setUserProfileDialog({
                        open: true,
                        userId: user.id,
                        userName: user.display_name,
                        userRole: user.role,
                      })
                    }
                  >
                    <Eye className="h-4 w-4 mr-1" />
                    {t("admin.viewProfile")}
                  </Button>
                  <Button
                    variant="outline"
                    size="sm"
                    className="text-green-600 border-green-300 hover:bg-green-50"
                    onClick={() => unbanUser.mutate(user.id)}
                  >
                    <UserCheck className="h-4 w-4 mr-1" />
                    {t("admin.unban")}
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      ) : (
        <div className="text-center py-12 text-muted-foreground">
          <Ban className="h-12 w-12 mx-auto mb-4 opacity-50" />
          <p>{searchQuery ? t("admin.noSearchResults") : t("admin.noBannedUsers")}</p>
        </div>
      )}

      <UserProfileDialog
        open={userProfileDialog.open}
        onOpenChange={(open) => setUserProfileDialog({ ...userProfileDialog, open })}
        userId={userProfileDialog.userId}
        userName={userProfileDialog.userName}
        userRole={userProfileDialog.userRole}
      />
    </div>
  );
};

export default AdminBannedUsers;
